/**
 *  Timeline
 *     
 *  : <Timeline items=[operation1, operation2, ...] />
 *  operation -    { id, title, date, amount, currency }
 * : items -   operations
 */

import TimelineItem from "../TimelineItem/TimelineItem";

const Timeline: React.FC<any> = ({ items }) => {
  let title = "";

  if (items) {
    if (items.length) {
      title = " ";
    } else {
      title = "     ";
    }
  }

  return (
    <>
      <h2>{title}</h2>
      {items ? (
        items.map((operation) => (
          <TimelineItem
            key={operation.id}
            id={operation.id}
            title={operation.title}
            date={operation.date}
            amount={operation.amount}
            currency={operation.currency}
          />
        ))
      ) : (
        <h2>,  </h2>
      )}
    </>
  );
};

export default Timeline;
