import React from "react";
import { NavLink } from "react-router-dom";

import BoardItem from "../BoardItem/BoardItem";

import sortAccounts from "../../helpers/sortAccounts";

import styles from "./Board.module.css";

/**
 *       Board   Board.ts
 *
 *  Board
 *  : <Board accounts={[ account1, account2, ... ]} />
 * : accounts -   
 *     .   -   (debit) =>  (credit) =>    (external) =>  (saving) =>  (loan)
 *      ,      RUB => USD => EUR => GBP
 */
/*
  *
         .
        NavLink.

               
  */

const Board: React.FC<any> = ({ accounts }) => {
  const accs = accounts ? sortAccounts(accounts) : [];

  return (
    <div className={styles.board}>
      {accs.length ? (
        <>
          {accs.map((acc) => (
            <NavLink
              to={`/account/${acc.id}`}
              className={styles.link}
              activeClassName={styles.activeItem}
              key={acc.id + new Date() + Math.random() * 100}
            >
              <BoardItem
                key={acc.id}
                type={acc.type}
                amount={acc.amount}
                currency={acc.currency}
                title={acc.title}
                customTitle={acc.customTitle}
              />
            </NavLink>
          ))}
          <NavLink
            to="/actions/add_card"
            className={styles.link}
            activeClassName={styles.activeItem}
          >
             
          </NavLink>
        </>
      ) : (
        <span>  </span>
      )}
    </div>
  );
};

export default Board;
